/* ------------- Navbar --------------------- */

.navbar {
  background-color: #0078bf !important;
  height: 75px;
  display: flex;
  align-items: center;
}

.logoContainer {
  display: flex;
}

.authContainer {
  display: flex;
  justify-content: flex-end;
}

.authContainer a {
  color: white;
  text-decoration: none;
  transition: 0.5s;
}

.authContainer a::after {
  content: '';
  display: block;
  width: 0;
  height: 2px;
  background: #fff;
  opacity: 0;
  transition: width 0.3s;
}

.authContainer a:hover::after {
  width: 100%;
  transition: width 0.3s;
  text-decoration: none !important;
  opacity: 1;
}

.dropdownItem div {
  color: #0078bf;
}

.dropdownItem:hover {
  background: #0078bf !important;
}

.dropdownItem div:hover {
  color: #fff !important;
}

/*------------------------------ Sidebar -------------------- */

.logoContainer ul {
  position: fixed;
  top: 75px;
  left: 0;
  margin: 0;
  padding: 50px 20px;
  box-sizing: border-box;
  width: 300px;
  height: 100vh;
  background: #4f6d7a;
  z-index: 10000000;
  overflow-y: auto;
  transition: left 0.5s;
}

.open ~ ul {
  left: -300px;
}

.logoContainer ul li {
  list-style: none;
}

.logoContainer ul li a {
  display: block;
  color: #dbe9ee;
  padding: 10px;
  box-sizing: border-box;
  text-decoration: none;
  font-size: 24px;
  font-weight: 700;
  transition: 0.5s;
}

.logoContainer ul li a:hover {
  transform: translateX(15px);
  color: #ffffff;
}

.active {
  color: yellow !important;
}

/* ------------------- Hamburguer clases ----------------------*/

/*!
 * Hamburgers
 * @description Tasty CSS-animated hamburgers
 * @author Jonathan Suh @jonsuh
 * @site https://jonsuh.com/hamburgers
 * @link https://github.com/jonsuh/hamburgers
 */
.hamburger {
  padding: 15px 15px;
  display: inline-block;
  cursor: pointer;
  transition-property: opacity, filter;
  transition-duration: 0.15s;
  transition-timing-function: linear;
  font: inherit;
  color: inherit;
  text-transform: none;
  background-color: transparent;
  border: 0;
  margin: 0;
  overflow: visible;
  max-height: 60px;
  position: relative;
  top: 10px;
  margin-right: 2%;
}
.hamburger:hover {
  opacity: 0.7;
}
.hamburger.isActive:hover {
  opacity: 0.7;
}
.hamburger.isActive .hamburgerInner,
.hamburger.isActive .hamburgerInner::before,
.hamburger.isActive .hamburgerInner::after {
  background-color: #fff;
}

.hamburgerBox {
  width: 40px;
  height: 24px;
  display: inline-block;
  position: relative;
}

.hamburgerInner {
  display: block;
  top: 50%;
  margin-top: -2px;
}
.hamburgerInner,
.hamburgerInner::before,
.hamburgerInner::after {
  width: 40px;
  height: 4px;
  background-color: #fff;
  border-radius: 4px;
  position: absolute;
  transition-property: transform;
  transition-duration: 0.15s;
  transition-timing-function: ease;
}
.hamburgerInner::before,
.hamburgerInner::after {
  content: '';
  display: block;
}
.hamburgerInner::before {
  top: -10px;
}
.hamburgerInner::after {
  bottom: -10px;
}

.hamburger3dx .hamburgerBox {
  perspective: 80px;
}

.hamburger3dx .hamburgerInner {
  transition: transform 0.15s cubic-bezier(0.645, 0.045, 0.355, 1),
    background-color 0s 0.1s cubic-bezier(0.645, 0.045, 0.355, 1);
}
.hamburger3dx .hamburgerInner::before,
.hamburger3dx .hamburgerInner::after {
  transition: transform 0s 0.1s cubic-bezier(0.645, 0.045, 0.355, 1);
}

.hamburger3dx.isActive .hamburgerInner {
  background-color: transparent !important;
  transform: rotateY(180deg);
}
.hamburger3dx.isActive .hamburgerInner::before {
  transform: translate3d(0, 10px, 0) rotate(45deg);
}
.hamburger3dx.isActive .hamburgerInner::after {
  transform: translate3d(0, -10px, 0) rotate(-45deg);
}

/*
       * 3DX Reverse
       */
.hamburger3dx-r .hamburgerBox {
