/*
       * Minus
       */
.hamburger--minus .hamburgerInner::before,
.hamburger--minus .hamburgerInner::after {
  transition: bottom 0.08s 0s ease-out, top 0.08s 0s ease-out, opacity 0s linear;
}

.hamburger--minus.isActive .hamburgerInner::before,
.hamburger--minus.isActive .hamburgerInner::after {
  opacity: 0;
  transition: bottom 0.08s ease-out, top 0.08s ease-out, opacity 0s 0.08s linear;
}

.hamburger--minus.isActive .hamburgerInner::before {
  top: 0;
}

.hamburger--minus.isActive .hamburgerInner::after {
  bottom: 0;
}

/*
       * Slider
       */
.hamburger--slider .hamburgerInner {
  top: 2px;
}
.hamburger--slider .hamburgerInner::before {
  top: 10px;
  transition-property: transform, opacity;
  transition-timing-function: ease;
  transition-duration: 0.15s;
}
.hamburger--slider .hamburgerInner::after {
  top: 20px;
}

.hamburger--slider.isActive .hamburgerInner {
  transform: translate3d(0, 10px, 0) rotate(45deg);
}
.hamburger--slider.isActive .hamburgerInner::before {
  transform: rotate(-45deg) translate3d(-5.71429px, -6px, 0);
  opacity: 0;
}
.hamburger--slider.isActive .hamburgerInner::after {
  transform: translate3d(0, -20px, 0) rotate(-90deg);
}

